import React from 'react';

const Progress = (props) => {

    

    return props.bars.map( (bar,idx) => {

        let answered = props.done[bar.title];
        let percent = Math.floor( answered/bar.total * 100);
       
        

        return (
        
        <div key={idx} className="ui green progress">
            <div className="bar" style={{width: `${percent}%`}} >
            <div className="progress"></div>
            </div>
            <div className="label">{bar.title}</div>
        </div>

        )
    })

}

export default Progress;