import React from 'react';
import _ from 'lodash';

import QuestionPanel from './QuestionPanel';
import Levels from './Levels';
import Progress from './Progress';
import Questions from '../questions/Questions';
import Header from './Header'
import Summary from './Summary'


const levels = [
    {
        title:"Object",
        total:Questions.Object.length
    },
    {
        title:"Array",
        total:Questions.Array.length
    },
    {
        title:"String",
        total:Questions.String.length
    },
    {
        title:"Number",
        total:Questions.Number.length
    },
    {
        title:"Boolean",
        total:Questions.Boolean.length
    },
    {
        title:"Math",
        total: Questions.Math.length
    }

]


let done = {

    Array:0,
    Object:0,
    Number:0,
    String:0,
    Boolean:0,
    Math:0
}



class App extends React.Component{
    constructor(props){
        super(props)


        this.state = {
            questions:[],
            currentQuestions:[],
            levels:levels,
            currentLevel:"",
            done:done,
            current: 0,
            answered:false,
            correct:false,
            showSummary:false
            
        };

    }

    setLevel = (level) => {

        let shuffled = _.shuffle(Questions[level])

        this.setState({currentQuestions:shuffled, currentLevel: level, current: 0});
    }

    componentDidMount(){

        this.setState({questions:Questions})
        this.setLevel("Object")
    }
   

    checkAnswer = (answer) => {

        //filter out spaces as answer key uses no space format
        answer = answer.split("").filter(char => char !== " ").join("");
        //replace double quote with single as answers will be in that format
        answer = answer.replace(/"/g, "'");
    
        this.setState({answered:true})


        if(answer === this.state.currentQuestions[this.state.current].answer || answer === this.state.currentQuestions[this.state.current].alt){

            //if answered correctly set relevant question to done:true
            let doneQuestions = this.state.questions[this.state.currentLevel].map( question => {
                return  ( question.answer === answer || question.alt === answer ) ? {...question, done:true } : question 
            });

            this.setState({ done: done, correct:true, questions: {...this.state.questions, [this.state.currentLevel]: doneQuestions } })

            let newQuestions = this.state.currentQuestions.map(question => {
                if(question.answer === answer){

                    let done = this.state.done;
                    done[this.state.currentLevel] += 1;
                    
                    return {...question, done:true, correct:true};
                }
                else
                    return question;
            })
           
            this.setState( { currentQuestions: newQuestions } , () =>{
                
                setTimeout( ()=> { this.getNextQuestion(); }, 500)
        
            } )
            
        }else{
            setTimeout( ()=> { this.getNextQuestion(); }, 500)
        }

        

    }

    getNextQuestion(){

        this.setState({answered:false, correct:false})
        
        console.log("In get next question squestions",this.state.currentQuestions, "current",this.state.current)
        //if there remain questions not correctly answered
        if(this.state.currentQuestions.some( question => question.done === false)){

            //if we have seen all questions filter down to not correctly answered and start on those
            if(this.state.current === this.state.currentQuestions.length -1){

                
                let newQuestions = this.state.currentQuestions.filter(question => {
                    return question.done === false;
                })

                console.log("Got to end so filtering down to", newQuestions)

                this.setState({currentQuestions:newQuestions, current:0})

            }else{
                this.setState({current: this.state.current + 1})
            }

        }else{

            this.setState({currentQuestions:[{prompt:"Finished!"}] , current:0})
        }

    }


    render(){
        return (
        <div>

        <Header />
            

        <div className="ui container" style={{marginTop:"10%"}}>
            <div className="ui grid">
                <div className="ui column grid center aligned">
                
                    <div className="four wide column">
                        <Progress done={this.state.done} bars={levels}/>
                    </div>
                    <div className="eight wide column">
                        <QuestionPanel checkAnswer={this.checkAnswer} question={this.state.currentQuestions[this.state.current]} correct={this.state.correct} answered={this.state.answered} />
                    </div>
                    <div className="four wide column">
                        <Levels levels={levels} setLevel={this.setLevel} />
                    </div>
                    <div>
                        <Summary questions={this.state.questions} levels={this.state.levels} show ={this.state.showSummary} showSummary={() => this.setState({showSummary: this.state.showSummary ? false : true }) }/>
                    </div>
                    
                </div>
            </div>
        </div>
        </div>
        )
    }


}

export default App;