import React from 'react';

import HowTo from './HowTo';

const image = <img alt="logo" src={process.env.PUBLIC_URL + '/logo.png'} style={{width:'20%',height:'10%'}} />;

const howStyle = {
    position:'relative',
    left:'80%',
    top: '10%',
    fontWeight:'bold',
    fontSize:'1.2rem'
    
}

class Header extends React.Component{
    state = {show: false};

    showHowTo = () => {
       
        this.setState({ show: this.state.show ? false: true })
    }


    render(){

        return (
        <div>
            {this.state.show ? <HowTo show={this.showHowTo}/> : null}
            {image}
            <div   onClick={this.showHowTo} style={howStyle}>How To Use</div>
            <hr style={{width:'85%'}} />
        </div>
        )
    }

}


export default Header;