import React from 'react';

const bStyle = {
    width:"100%",
    margin:"2px",
    display:"block"
}

const Levels = (props) => {


    return props.levels.map( (level,idx) => {
        return <div key={idx}><button  style={bStyle} onClick={props.setLevel.bind(null,level.title)} className="ui fluid button large">{level.title}</button></div>
    })

    
}

export default Levels;