import React from 'react';

const panelStyle = {
    border:"solid grey 1px",
    borderRadius:"5px",
    width:"100%x",
    height:"50%",
    fontSize: "1.5em"
}

const correctStyle = {
    fontSize:"2em",
    background:"#00cc00"
}

class QuestionPanel extends React.Component{
    constructor(props){
        super(props)

        this.state = { input:'', show: false}

    }

    onChange = (e) => {

        this.setState({input:e.target.value})
    }

    onClick = () => {

        this.props.checkAnswer(this.state.input);
        //console.log(eval('[1,2,3].push(4)'))
        this.setState({input:'', show:false})

    }

    renderAnswer = () => {

        if(!this.state.show)
            return;

       return ( 
       
       <div className="ui message">
            {this.props.question.answer}
        </div>
       )
    }


    render(){

        if(this.props.answered){
            return (
                <div style={ this.props.correct ? correctStyle: {}} className="ui floating message">
                    <p>{this.props.correct? "Correct!": "Incorrect"}</p>

                </div>
            )
        }

        return (

            <div className="ui container center aligned" style={panelStyle}>
                <div className="ui message" style={{padding:"50px"}}>
                    {this.props.question ? this.props.question.prompt : "Loading"}
                </div>
                <div className="ui fluid input" style={{padding:"20px"}}>
                    <input autoFocus type="text" onKeyPress={(e) => {if(e.key === 'Enter') {this.onClick()}}} value={this.state.input} onChange={this.onChange}></input>
                    <button className="ui button primary" onClick={this.onClick}>Submit</button>
                </div>
                <button onClick={() => this.setState({show: this.state.show ? false : true })} className="ui button mini">Show Answer</button>
                {this.renderAnswer()}
            </div>
            
        ) 
    }


}

export default QuestionPanel;