import React from 'react';

const style = {
    margin:"3em",
    display:'block'
}

const ulStyle = {

    listStyle:'none',
    margin:'1em'
}


const summaryStyle = {
    position:'relative',
    top:'0',
    margin:'auto',
    border:'solid rgba(34,36,38,.15) 2px',
    zIndex:'2',
    borderRadius:'5px',
    display:'flex',
    flexWrap:'wrap',
    textAlign:'left',
    padding:'1em',
}



function processUndone(questions, {title} ){

    let undone = questions[title].filter( question => !question.done ).map( (question, idx) => {
        return <li key={idx}>{question.answer}</li>
    });

    console.log("undone is",undone)
    return undone;
}



const Summary = (props) => {


    if(props.show){

        let list = props.levels.map((level, id) => {
           return (
            <div style={ulStyle} className="ui segment">
                <h3 className="ui label huge">{level.title}</h3>
                <ul style={ulStyle} >{processUndone(props.questions,level)}</ul>
            </div> 
           )
        });
        console.log("list is here",list)
         
        return (
                <div >
                    <div style={style} onClick={props.showSummary} className="ui button primary">Close Summary</div>
                    <h1 className="ui label huge">Unanswered Items</h1>
                    
                        <div style={summaryStyle} className="ui container">
                        
                        {list}
                        </div>
                    
                </div>
                )
    }
    

    return <div style={style} onClick={props.showSummary} className="ui button primary">Summary</div>

}

export default Summary;